@media (min-width: 992px) {
  .header-other li.nav-item {
    position: relative;
  }
  .header-other li.nav-item:not(:last-child):after {
    content: "";
    position: absolute;
    height: 17px;
    width: 2px;
    background-color: rgba(116, 116, 116, 0.1);
    top: 10px;
  }
}

.header {
  position: -webkit-sticky;
  position: sticky;
  overflow: hidden;
  top: 0;
  background-color: #fff;
  z-index: 100;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 6%);
}

.header-link .nav-link:hover {
  color: var(--color-primary) !important;
}

input[type="checkbox"] {
  display: none;
}

.button {
  position: relative;
  display: block;
  width: 50px;
  height: 26px;
  background-color: var(--color-primary);
  border-radius: 350px;
  cursor: pointer;
  direction: ltr;
}

.knob {
  width: 22px;
  height: 22px;
  background: #fff;
  background-size: 100px;
  position: relative;
  top: 2px;
  left: 2px;
  border-radius: 290px;
  transition: 0.4s ease left, 0.4s ease background-position;
  z-index: 2;
}

.subscribe,
.alright {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
  font-size: 10px;
  font-weight: bold;
  margin-left: 30px;
  z-index: 1;
}

.alright {
  margin-left: 10px;
}

.lol-checkbox:checked + .button .knob {
  left: 26px;
  background-position: -350px 0;
}

.lol-checkbox {
  display: none;
}

.scroll_to_top {
  bottom: 2rem;
  width: 3rem;
  height: 3rem;
  font-size: 1.5rem;
  transition: all 0.5s;
  z-index: 1000;
  background-color: var(--color-primary);
  display: flex;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  border: none;
  outline-offset: 0.3rem;
  transform: translateY(100vh);
}
.scroll_to_top:hover {
  background-color: var(--color-primary-dark);
}

.acitve.scroll_to_top {
  transform: translateY(0);
}
